import UniLoadMore from '@/components/UniLoadMore/UniLoadMore'
export default {
	components:{
		UniLoadMore,
	},
	data(){
		return {
			noData:'more',
			page:1,
			listRows:10,
			dataList:[],
		}
	},
	onShow(){
		this.page = 1;
		this.listRows = 10;
		this.dataList = [];
		this.reqStoreList();
	},
	onReachBottom(){
		if(this.noData === 'more'){
			this.reqStoreList()
		}else{
			return false
		}
	},
	methods:{
		reqStoreList(){
			this.noData = 'loading';
			const TOKEN = uni.getStorageSync('access_token');
			this.$request.GET({
				url:this.$api.apiUrl.GET_V6_SHOP_LISTING,
				data:{
					token:TOKEN,
					page:this.page,
					listRows:this.listRows
				}
			}).then(res=>{
				if(res.code === 'SUCCESS'){
					if(res.data.length < 10){
						this.noData = 'noMore';
					}else{
						// 如果还有数据就让page+1
						this.page++;
						this.noData = 'more';
					}
					this.dataList = this.dataList.concat(res.data);
					this.dataList = this.computedQuickSort;
				}else{
					this.$api.msg(res.msg)
				}
			})
		},
	},
	computed:{
		// 排序
		computedQuickSort(){
			function quickSort(arr){
				if(arr.length <= 1){return arr;}
				let temp = arr[0];
				const left = [];
				const right = [];
				for(var i = 1; i < arr.length; i++){
					if(arr[i].total_price > temp.total_price){
						left.push(arr[i]);
					}else{
						right.push(arr[i]);
					}
				}
				return quickSort(left).concat([temp], quickSort(right));
			}
			return quickSort(this.dataList);
		}
	}
}
